import React from "react";
import Header from "../templates/Header";
import BookList from "../components/BookList";

import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';

import Axios from "axios";

// install Swiper components
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

class Main extends React.Component{
    state = {
        slider: [],
        bookList: []
    };

    updateSlider = () => {
        let slider = [
            {
                key:0,
                url:'/img/banner.png',
                urlMobile:'/img/banner_mobile.png',
                color: '#111',
                description: '책 출간 기념'
            }
        ];
       this.setState({slider: slider});
    };

    updateBookList = async () => {
        let url = "https://moe.work/bbs/ajax.board.list.php?bo_table=book_publishing&mode=json";
        const dataMercine = await Axios.get(url);
        console.log(dataMercine.data.data);

        this.setState({bookList: dataMercine.data.data});
    }

    componentDidMount = () => {
        this.updateSlider();
        this.updateBookList();
    }


    render() {
        const { slider, bookList } = this.state;

        return (
            <>
                <Header />
                <div id="mainSlider" className="text-center">
                    { slider.length===0?
                        '슬라이더 로딩 중입니다...':
                        <Swiper
                            onSlideChange={() => console.log('slide change')}
                            onSwiper={(swiper) => console.log(swiper)}
                            navigation
                            loop
                            rebuildOnUpdate={true}
                        >
                            {slider.map(result => {
                                return <SwiperSlide key={result.key}>{({isActive}) => (
                                    <div style={{backgroundColor: "" + result.color + ""}}>
                                        <img src={result.url} alt={result.description} className="view-pc"/>
                                        <img src={result.urlMobile} alt={result.description}
                                             className="view-tablet-mobile"/>
                                    </div>
                                )}</SwiperSlide>
                            })}
                        </Swiper>
                    }
                </div>

                <div id="main_booklist" className="m-t-20 p-l-20 p-r-20">
                    <div className="containerWrapper">
                        <h1>발매한 책 리스트</h1>
                        { bookList.length===0 ? '책 목록을 가져오는 중입니다.' : bookList.map(result => {
                           return <BookList name={result.wr_subject} thumb={result.thumb} />
                        }) }
                    </div>
                </div>
            </>
        )
    }
}

export default Main;