import React from "react";

function BookList({name, thumb}){

    return (
        <div className="bookLayer">
            <div className="bookImage">
                <img src={thumb} alt={{name}+"도서 이미지"} />
            </div>
            <div className="bookName">
                {name}
            </div>
        </div>
    )

}

export default BookList;